// Handles information about menu
import elementCreator from "./elementCreator.js";

const menuModule = {
  fillContent: function (mainElement) {
    const mainWrapper = elementCreator.createElement(
      "div",
      {
        class: "main-wrapper",
        id: "main-wrapper",
      },
      []
    );

    const title = elementCreator.createElement(
      "div",
      {
        class: "menu-item",
        id: "title",
      },
      ["Our Menu"]
    );

    const menu = elementCreator.createElement(
      "div",
      {
        class: "menu-item",
        id: "burger",
      },
      [`Star burger: $8.50`, `Beans: $1`]
    );

    const hours = elementCreator.createElement(
      "div",
      {
        class: "menu-item",
        id: "fries",
      },
      []
    );

    const location = elementCreator.createElement(
      "div",
      {
        class: "menu-item",
        id: "location",
      },
      [`Located somewhere near you!  Check Google maps to find one.`]
    );

    mainElement.appendChild(mainWrapper);
    mainWrapper.appendChild(title);
    mainWrapper.appendChild(menu);
    mainWrapper.appendChild(hours);
    mainWrapper.appendChild(location);
  },
};

export default homeModule;
